import {
  IconButton,
  makeStyles,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
} from "@material-ui/core";
import React, { useState } from "react";
import FlagIcon from "@material-ui/icons/Flag";
import CheckIcon from "@material-ui/icons/Check";
import ClearIcon from "@material-ui/icons/Clear";
import VisibilityIcon from "@material-ui/icons/Visibility";
import moment from "moment";
import { ItemResults } from "../Data/Items";
import { ItemReview } from "./ItemReview";

const useStyles = makeStyles({
  centered: {
    textAlign: "center",
  },
});

export interface ItemsTableProperties {
  sectionId: string;
}

export const ItemsTable = ({ sectionId }: ItemsTableProperties) => {
  const classes = useStyles();
  const itemData = ItemResults.filter((i) => i.section === sectionId);

  const maxItem = Math.max(...itemData.map((i) => i.number));
  const [reviewItem, setReviewItem] = useState<number | undefined>();

  return (
    <>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell className={classes.centered}>
              <Typography>#</Typography>
            </TableCell>
            <TableCell className={classes.centered}>
              <CheckIcon />
            </TableCell>
            <TableCell>
              <Typography>Preview</Typography>
            </TableCell>
            <TableCell className={classes.centered}>
              <Typography>Time</Typography>
            </TableCell>
            <TableCell className={classes.centered}>
              <FlagIcon />
            </TableCell>
            <TableCell className={classes.centered}>
              <VisibilityIcon />
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {itemData.map((item) => (
            <TableRow key={item.number}>
              <TableCell className={classes.centered}>
                <Typography>{item.number}</Typography>
              </TableCell>
              <TableCell className={classes.centered}>
                {item.correct ? <CheckIcon /> : <ClearIcon />}
              </TableCell>
              <TableCell>
                <Typography>{item.preview}</Typography>
              </TableCell>
              <TableCell className={classes.centered}>
                <Typography>
                  {moment
                    .utc(
                      moment
                        .duration(item.timeInSeconds, "seconds")
                        .asMilliseconds()
                    )
                    .format("m:ss")}
                </Typography>
              </TableCell>
              <TableCell className={classes.centered}>
                {item.flagged && <FlagIcon />}
              </TableCell>
              <TableCell className={classes.centered}>
                <IconButton onClick={() => setReviewItem(item.number)}>
                  <VisibilityIcon />
                </IconButton>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <ItemReview
        open={reviewItem !== undefined}
        onClose={() => setReviewItem(undefined)}
        section={sectionId}
        itemNumber={reviewItem}
        canNext={(reviewItem || 0) < maxItem}
        canPrevious={(reviewItem || 0) > 1}
        onNext={() => setReviewItem((reviewItem || 0) + 1)}
        onPrevious={() => setReviewItem((reviewItem || 0) - 1)}
        onFirst={() => setReviewItem(1)}
        onLast={() => setReviewItem(maxItem)}
        title={`Item ${reviewItem} of ${maxItem}`}
      />
    </>
  );
};
