import { Box, makeStyles, Typography } from "@material-ui/core";
import React from "react";
import { SkillsGroups } from "../Data/Skills";
import { ContentAreaBarGraph } from "../Graphics/ContentAreaBarGraph";
import { SkillsBreakdownTable } from "./SkillsBreakdownTable";

const useStyles = makeStyles({
  verticalStack: {
    display: "flex",
    flexDirection: "column",
  },
  horizontalStack: {
    display: "flex",
    flexDirection: "row",
  },
  skillsGroup: {
    marginTop: 15,
    marginBottom: 15,
  },
  graphContainer: {
    marginTop: "auto",
    marginBottom: "auto",
  },
  overallLabel: {
    marginTop: 30,
    paddingBottom: 10,
    borderBottom: "1px solid lightgray",
  },
  mainLabel: {
    marginTop: "auto",
    marginBottom: "auto",
    marginLeft: 30,
    marginRight: 30,
  },
});

export const SkillsBreakdown = () => {
  const classes = useStyles();

  return (
    <Box>
      {SkillsGroups.map((group, index) => (
        <Box
          key={index}
          className={`${classes.verticalStack} ${classes.skillsGroup}`}
        >
          {group.label && (
            <Box
              className={`${classes.horizontalStack} ${classes.overallLabel}`}
            >
              <Typography
                variant="h4"
                display="inline"
                className={classes.mainLabel}
              >
                {group.label}
              </Typography>
              <Typography variant="subtitle1" display="inline">
                {group.description}
              </Typography>
            </Box>
          )}
          <Box className={classes.horizontalStack}>
            <Box
              className={`${classes.verticalStack} ${classes.graphContainer}`}
            >
              <ContentAreaBarGraph
                width={300}
                height={300}
                margin={{ top: 50, left: 10, bottom: 50, right: 10 }}
                skills={group.skills}
              />
            </Box>
            <SkillsBreakdownTable skills={group.skills} />
          </Box>
        </Box>
      ))}
    </Box>
  );
};
